.container {
	background-color: var(--extralight-gray);
}

.container > div > h2 {
	font-weight: 600;
	font-size: 40px;
	line-height: 56px;
	color: var(--black);
	text-align: center;
}

@media screen and (max-width: 375px) {
	.container > div > h2  {
		font-size: 24px;
		line-height: 32px;
	}
}